import dayjs from 'dayjs'
import 'dayjs/locale/ko'
import { fontList, themeColorList } from '@/data/domain/custom'

interface Props {
    selectedFontIndex: number
    selectedThemeColorIndex: number
    maleName: string
    maleRelation: string
